"use client"

import { useState, useEffect, useRef, useCallback } from 'react'
import { Play, Pause, RotateCcw } from 'lucide-react'

interface Obstacle {
  id: number
  x: number
  lane: number
  type: 'train' | 'barrier'
}

interface Coin {
  id: number
  x: number
  lane: number
  collected: boolean
}

export default function SubwaySurfer() {
  const [isPlaying, setIsPlaying] = useState(false)
  const [score, setScore] = useState(0)
  const [highScore, setHighScore] = useState(0)
  const [playerLane, setPlayerLane] = useState(1)
  const [isJumping, setIsJumping] = useState(false)
  const [obstacles, setObstacles] = useState<Obstacle[]>([])
  const [coins, setCoins] = useState<Coin[]>([])
  const [gameSpeed, setGameSpeed] = useState(5)
  const [gameOver, setGameOver] = useState(false)
  
  const gameLoopRef = useRef<number>()
  const obstacleSpawnRef = useRef<number>()
  const coinSpawnRef = useRef<number>()
  
  const lanes = [0, 1, 2]
  const laneWidth = 100
  const playerY = isJumping ? -30 : 0

  // Load high score from localStorage
  useEffect(() => {
    const saved = localStorage.getItem('subwaySurferHighScore')
    if (saved) setHighScore(parseInt(saved))
  }, [])

  // Handle keyboard input
  useEffect(() => {
    const handleKeyPress = (e: KeyboardEvent) => {
      if (!isPlaying || gameOver) return
      
      switch(e.key) {
        case 'ArrowLeft':
        case 'a':
          setPlayerLane(prev => Math.max(0, prev - 1))
          break
        case 'ArrowRight':
        case 'd':
          setPlayerLane(prev => Math.min(2, prev + 1))
          break
        case 'ArrowUp':
        case 'w':
        case ' ':
          if (!isJumping) {
            setIsJumping(true)
            setTimeout(() => setIsJumping(false), 500)
          }
          break
      }
    }

    window.addEventListener('keydown', handleKeyPress)
    return () => window.removeEventListener('keydown', handleKeyPress)
  }, [isPlaying, gameOver, isJumping])

  // Spawn obstacles
  useEffect(() => {
    if (!isPlaying || gameOver) return
    
    const spawnObstacle = () => {
      const newObstacle: Obstacle = {
        id: Date.now(),
        x: 400,
        lane: Math.floor(Math.random() * 3),
        type: Math.random() > 0.7 ? 'train' : 'barrier'
      }
      setObstacles(prev => [...prev, newObstacle])
    }

    obstacleSpawnRef.current = window.setInterval(spawnObstacle, 2000)
    return () => {
      if (obstacleSpawnRef.current) clearInterval(obstacleSpawnRef.current)
    }
  }, [isPlaying, gameOver])

  // Spawn coins
  useEffect(() => {
    if (!isPlaying || gameOver) return
    
    const spawnCoin = () => {
      const newCoin: Coin = {
        id: Date.now(),
        x: 400,
        lane: Math.floor(Math.random() * 3),
        collected: false
      }
      setCoins(prev => [...prev, newCoin])
    }

    coinSpawnRef.current = window.setInterval(spawnCoin, 1500)
    return () => {
      if (coinSpawnRef.current) clearInterval(coinSpawnRef.current)
    }
  }, [isPlaying, gameOver])

  // Game loop
  useEffect(() => {
    if (!isPlaying || gameOver) return

    const gameLoop = () => {
      // Move obstacles
      setObstacles(prev => {
        const updated = prev.map(obstacle => ({
          ...obstacle,
          x: obstacle.x - gameSpeed
        })).filter(obstacle => obstacle.x > -50)

        // Check collisions
        updated.forEach(obstacle => {
          if (
            obstacle.x < 50 && obstacle.x > 10 &&
            obstacle.lane === playerLane &&
            !isJumping
          ) {
            setGameOver(true)
            setIsPlaying(false)
            if (score > highScore) {
              setHighScore(score)
              localStorage.setItem('subwaySurferHighScore', score.toString())
            }
          }
        })

        return updated
      })

      // Move coins
      setCoins(prev => {
        const updated = prev.map(coin => {
          if (
            !coin.collected &&
            coin.x < 50 && coin.x > 10 &&
            coin.lane === playerLane
          ) {
            setScore(s => s + 10)
            return { ...coin, collected: true }
          }
          return { ...coin, x: coin.x - gameSpeed }
        }).filter(coin => coin.x > -50 && !coin.collected)

        return updated
      })

      // Increase score and speed
      setScore(prev => prev + 1)
      setGameSpeed(prev => Math.min(15, prev + 0.001))

      gameLoopRef.current = requestAnimationFrame(gameLoop)
    }

    gameLoopRef.current = requestAnimationFrame(gameLoop)

    return () => {
      if (gameLoopRef.current) cancelAnimationFrame(gameLoopRef.current)
    }
  }, [isPlaying, gameOver, playerLane, isJumping, gameSpeed, score, highScore])

  const startGame = () => {
    setIsPlaying(true)
    setGameOver(false)
    setScore(0)
    setGameSpeed(5)
    setObstacles([])
    setCoins([])
    setPlayerLane(1)
    setIsJumping(false)
  }

  const resetGame = () => {
    setIsPlaying(false)
    setGameOver(false)
    setScore(0)
    setGameSpeed(5)
    setObstacles([])
    setCoins([])
    setPlayerLane(1)
    setIsJumping(false)
  }

  return (
    <div className="w-full max-w-2xl mx-auto">
      <div className="rounded-lg border border-border bg-card/80 backdrop-blur-sm overflow-hidden shadow-xl">
        {/* Game Header */}
        <div className="flex items-center justify-between px-4 py-3 bg-gradient-to-r from-purple-600 to-pink-600">
          <div className="flex items-center gap-4">
            <h3 className="text-white font-bold text-lg">Subway Surfer</h3>
            <div className="flex gap-2">
              <span className="text-white/80 text-sm">Score: {score}</span>
              <span className="text-white/80 text-sm">High: {highScore}</span>
            </div>
          </div>
          <div className="flex gap-2">
            {!isPlaying ? (
              <button
                onClick={startGame}
                className="text-white hover:text-white/80 transition-colors"
              >
                <Play size={20} />
              </button>
            ) : (
              <button
                onClick={() => setIsPlaying(false)}
                className="text-white hover:text-white/80 transition-colors"
              >
                <Pause size={20} />
              </button>
            )}
            <button
              onClick={resetGame}
              className="text-white hover:text-white/80 transition-colors"
            >
              <RotateCcw size={20} />
            </button>
          </div>
        </div>

        {/* Game Area */}
        <div className="relative h-64 bg-gradient-to-b from-sky-400 to-sky-600 overflow-hidden">
          {/* Translucent overlay image */}
          <div className="absolute inset-0 opacity-20 pointer-events-none z-10">
            <div className="relative w-full h-full">
              <img
                src="/images/screenshot-202025-12-01-20at-205.png"
                alt="Subway Surfer overlay"
                className="absolute inset-0 w-full h-full object-cover"
              />
            </div>
          </div>

          {/* Game Canvas */}
          <div className="relative z-20 w-full h-full">
            {/* Road lanes */}
            <div className="absolute bottom-0 left-0 right-0 h-32 bg-gray-800">
              {lanes.map(lane => (
                <div
                  key={lane}
                  className="absolute top-0 bottom-0 border-r-2 border-dashed border-yellow-400"
                  style={{
                    left: `${lane * laneWidth + laneWidth}px`,
                    width: '2px'
                  }}
                />
              ))}
            </div>

            {/* Player */}
            <div
              className="absolute bottom-20 transition-all duration-200 z-30"
              style={{
                left: `${playerLane * laneWidth + 25}px`,
                transform: `translateY(${playerY}px)`
              }}
            >
              <div className="w-12 h-12 bg-gradient-to-br from-blue-500 to-purple-600 rounded-lg shadow-lg flex items-center justify-center">
                <div className="text-white font-bold text-xs">RUN</div>
              </div>
            </div>

            {/* Obstacles */}
            {obstacles.map(obstacle => (
              <div
                key={obstacle.id}
                className="absolute bottom-20 transition-none"
                style={{
                  left: `${obstacle.x}px`,
                  transform: `translateX(${obstacle.lane * laneWidth}px)`
                }}
              >
                {obstacle.type === 'train' ? (
                  <div className="w-16 h-16 bg-gradient-to-br from-red-600 to-red-800 rounded shadow-lg flex items-center justify-center">
                    <div className="text-white font-bold text-xs">TRAIN</div>
                  </div>
                ) : (
                  <div className="w-12 h-8 bg-gradient-to-br from-orange-500 to-orange-700 rounded shadow-lg"></div>
                )}
              </div>
            ))}

            {/* Coins */}
            {coins.map(coin => (
              <div
                key={coin.id}
                className="absolute bottom-24 transition-none"
                style={{
                  left: `${coin.x}px`,
                  transform: `translateX(${coin.lane * laneWidth + 35}px)`
                }}
              >
                <div className="w-6 h-6 bg-gradient-to-br from-yellow-400 to-yellow-600 rounded-full shadow-lg animate-pulse"></div>
              </div>
            ))}

            {/* Game Over Overlay */}
            {gameOver && (
              <div className="absolute inset-0 bg-black/70 flex items-center justify-center z-40">
                <div className="text-center">
                  <h3 className="text-white text-2xl font-bold mb-2">Game Over!</h3>
                  <p className="text-white/80 mb-4">Final Score: {score}</p>
                  <button
                    onClick={startGame}
                    className="px-4 py-2 bg-gradient-to-r from-purple-600 to-pink-600 text-white rounded-lg hover:opacity-90 transition-opacity"
                  >
                    Play Again
                  </button>
                </div>
              </div>
            )}

            {/* Start Screen */}
            {!isPlaying && !gameOver && (
              <div className="absolute inset-0 bg-black/50 flex items-center justify-center z-40">
                <div className="text-center">
                  <h3 className="text-white text-xl font-bold mb-2">Subway Surfer</h3>
                  <p className="text-white/80 text-sm mb-4">Use A/D or Arrow Keys to move, W/Space to jump</p>
                  <button
                    onClick={startGame}
                    className="px-4 py-2 bg-gradient-to-r from-purple-600 to-pink-600 text-white rounded-lg hover:opacity-90 transition-opacity"
                  >
                    Start Game
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Game Footer */}
        <div className="px-4 py-2 bg-gray-900 border-t border-gray-700">
          <div className="flex justify-between items-center">
            <div className="text-xs text-gray-400 font-mono">
              Speed: {gameSpeed.toFixed(1)}x
            </div>
            <div className="text-xs text-gray-400 font-mono">
              Controls: ← → or A/D to move, ↑ or W/Space to jump
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
